    id: "306",
    description: ["I love love love homemade pizza, and this recipe is SO yummy! The crust gets super crispy and bubbly.","We heat the oven to extremely hot and bake the pizza on the back of pre-heated sheet pans, and this allows the crust to get really well cooked, kind of like a brick oven. It's not the easiest pizza recipe, but we make this at least once every couple weeks because it's soooo good. "],
    notes: ["This recipe is definitely a 2 or more person effort. I can't get these pizzas in the oven safely without some help. On that note...","In order to get a yummy brick-oven pizza effect, of course we need to heat the oven to very hot temperatures. Please be careful and do not burn yourself or get hurt! I can't be responsible if you burn yourself!", "Okay now onto a happier note, I use a bread machine for this recipe and it makes it SO much easier to make the dough. I can avoid most of the kneading and just let the bread machine go. If you don't have one, we found ours at a thrift store for only 3 dollars! I highly recommend it for this recipe and for most bread."],
