     "Dump the water/yeast mixture in the mixing bowl, or if you are using the bread machine, the bread bowl. Add the 3.5 cups of flour and the salt. You don’t want to add the salt directly to the liquids as it can kill the yeast. Now you can add the melted butter on top. ",
    "Bread Mixer route: that’s it! Place in the bread mixer and let it do its magic. Make sure you select the “dough” setting, or it will bake it for you. If you don’t have a bread mixer, we got ours for 3 dollars a thrift store, so I highly recommend!"
,   "Non Bread Mixer route: Knead the dough for 10 minutes, then cover bowl with plastic wrap and set aside, letting it rise for one hour.",
"When the bread is done rising, (your bread machine will beep) sprinkle some flour on the counter and start kneading it again. I only do it 10-20 times and then form it into a ball, then slice it into two. With your hands, form each half into another roundish ball. This will later become your pizza, so just try to make it look as nice and round on the top as you can. ",
