"After about a half hour, uncover the dough. Sprinkle some more flour on the counter so you can spread your dough into your pizza crust. ",
"Start preheating the oven to 500 degrees. Place 2 large baking sheets upside down in the oven (or a pizza stone if you like!) so they can start to heat up as well. Please be careful not to burn yourself. The oven is going to get extremely hot. ","I avoid using a rolling pin, because this can lead to a kind of flat dough and destroy all your bubbles, so you won’t get those nice beautiful bubbles in the dough as it bakes. If you don’t like those, go for the rolling pin. ",
