<template>
    <div class="search">
        
        <div v-if="filtered.length >0" >
            <h2>Category: {{this.$route.params.category}}</h2>
            <div class="masonry">
                <div  class="masonry-item" v-for="recipe in filtered" :key="recipe.id">
                        <router-link :to="{ name: 'recipe', params: { id: recipe.id }}"><img class="masonry-pic" :src=recipe.img /></router-link>
                        <a href = ""></a>
                        <router-link :to="{ name: 'recipe', params: { id: recipe.id }}"><h3>{{recipe.name}}</h3></router-link>
                        
                </div>

            </div>
        </div>
        <div v-else-if="this.$root.$data.searching ==true">
            <h3>Sorry! There were no search results for "{{$route.params.data}}".</h3>

            <br>
