</template>

<script>
export default {
  name: 'Categories',
  components: {
  },
  created(){
      this.filterRecipes();
      this.filtered = this.$root.$data.filtered;
      console.log(this.filtered)
  },
  data(){
      return {
          recipes: this.$root.$data.recipes,
      }
  },
  computed:{
      filtered: function(){
          return this.$root.$data.filtered;
      },
  },
  methods:{
      filterRecipes() {
           let filtered = []
           this.$root.$data.recipes.forEach(recipe => {
               recipe.keywords.every(keyword => {
                    if (keyword.search(this.$route.params.category.toLowerCase())>=0){
                        filtered.push(recipe);
                        return false;
                    }
                    else{
                        return true;
                    }
               });
               
           });
           /*this.$root.$data.filtered = this.$root.$data.recipes.filter(recipe => recipe.keywords.includes(this.searchText.toLowerCase()));*/
           this.$root.$data.filtered = filtered;
           this.$root.$data.searching = true;
    },
},

}
</script>

